.card {
  position: relative;
}
.card::before {
  content: "";
  position: absolute;
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  top: 27px;
  left: 66px;
  transform: translate(-120%, -25%);
  width: 40px;
  height: 40px;
}
.card--house::before {
  background-image: url("../img/house-ico.svg");
}
.card--food::before {
  background-image: url("../img/food-ico.svg");
}
.card--gift::before {
  background-image: url("../img/gift-ico.svg");
}
@media (min-width: 768px) {
  .card::before {
    top: 50px;
    left: 184px;
    width: 80px;
    height: 74px;
  }
  .card--gift::before {
    width: 66px;
    height: 81px;
    margin: 5px 0 19px -4px;
  }
  .card--food::before {
    width: 78px;
    height: 81px;
    margin: 0 0 19px;
