.attraction__input:not(:disabled):not(:checked) + .attraction__control:hover::before {
  background-color: #e5e5e5;
}
/* стили для активного чекбокса (при нажатии на него) */
.attraction__input:not(:disabled):active + .attraction__control::before {
  background-color: #e5e5e5;
}
/* стили для чекбокса, находящегося в фокусе */
