.control__label {
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
}
.control {
  width: 280px;
  cursor: pointer;
  margin-left: 20px;
  font-weight: 700;
}
.control-input {
  /* Скрываем реальную радио кнопку*/
  width: 0;
  height: 0;
  position: absolute;
  opacity: 0;
  z-index: -1;
}
.control__mark {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border: 2px solid #f2f2f2;
  border-radius: 50%;
  vertical-align: middle;
  margin-right: 5px;
}
.control__mark:focus,
.control__mark:hover {
  background: #e5e5e5;
}
.control__mark:active {
  background-color: #d5d5d5;
}
.control__mark::before {
  content: "";
  /* Рисуем внешний круг */
  display: inline-block;
  width: 16px;
  height: 16px;
  background: #333333;
  border-radius: 50%;
  /* Выравниваем по центру */
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0);
  transition: 0.2s ease-in;
}
/* Переключения фейковой кнопки */
.control-input:checked + .control__mark::before {
  transform: translate(-50%, -50%) scale(1);
}
/* Подсветка для радиокнопке в фокусе */
.control-input:focus + .control__mark {
  box-shadow: 0 0 0 1px #a3a4a7, 0 0 10 #7e9bbd;
}
