  width: 100%;
}
.emotion__button {
  width: 280px;
  margin-top: 56px;
  padding: 16px 0;
  text-align: center;
}
@media (min-width: 768px) {
  .emotion__title {
    font-size: 21px;
    line-height: 26px;
    margin-top: 24px;
    position: relative;
    margin-bottom: 26px;
  }
  .emotion__title::after {
    position: absolute;
    top: 9px;
    left: 0;
    content: " ";
    width: 122px;
    margin-top: 2px;
    border: 0;
    border-top: 1px solid #d6d6d6;
  }
  .emotion__title::before {
    position: absolute;
    right: 0;
    top: 9px;
    content: " ";
    width: 122px;
    margin-top: 2px;
    border: 0;
    border-top: 1px solid #d6d6d6;
  }
  .emotion__wraper {
    width: 648px;
    margin: 0 auto;
    padding: 0 21px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .emotion__label {
    font-size: 17px;
    width: 617px;
    height: 129px;
  }
  .emotion__button {
    width: 324px;
    margin-top: 49px;
  }
  .emotion-field {
    padding-top: 69px;
    font-size: 14px;
  }
  .emotion {
    width: 100%;
    margin: -37px auto 65px;
  }
}
@media (min-width: 1200px) {
  .emotion__wraper {
    width: 768px;
  }
  .emotion__label {
    width: 737px;
    height: 128px;
  }
  .emotion__button {
    width: 384px;
  }
  .emotion__title::after {
    top: 9px;
    left: -95px;
    width: 278px;
  }
  .emotion__title::before {
    right: -97px;
    top: 9px;
    width: 278px;
  }
}
